<template>
  <div>
    <Head title="购物车" />
    
    <!-- 1 -->
    <div v-if="carflag">
   
      <van-card
        v-for="(item,i) in list"
        :key="i"
         :num="item.count"
        :price="item.goodInfo.price"
        :title="item.goodInfo.name"
        :thumb="item.goodInfo.img"
        class="goodsinfo"
      >
      
        <div slot="footer">
          <!-- <input type="checkbox" class="radio" @click="check()" /> -->

          <!-- <van-stepper v-model="item.count" class="numbox" /> -->
         
            <van-button size="mini"  class="yes"  @click="pay(item.goodInfo.id)">提交订单</van-button>
          <van-button size="mini" class="del" @click="del(item.goodInfo.id,i)">删除订单</van-button>
        </div>
       
      </van-card>
     

      <!--结算-->
      <!-- <van-submit-bar :price="money" button-text="提交订单" @submit="onSubmit">
        <van-checkbox v-model="checkedall" @click="checkAll()">全选</van-checkbox>
      </van-submit-bar> -->
    </div>

    <!-- 2 -->
    <div v-else>
      <van-button type="default" size="large" style="margin-top:300px;">购物车是空的，去逛逛吧~</van-button>
    
    </div>


      <Foot />
  </div>
</template>

<script>
import { Dialog } from "vant";
import { Toast } from 'vant';

export default {
  data() {
    return {
      value: "",

      checkedall: false,
      money: 0,
      list: [],
      obj: {},
      carflag: "",
      result: []
    };
  },
  created() {
    var username = window.localStorage.getItem("users");
    this.$axios
      .post("/vue/getShopcarInfo", { username: username })
      .then(res => {
        if (res.data.code == 200) {
          this.carflag = true;
          this.list = res.data.carList;
        } else if (res.data.code == 300) {
        
          this.carflag = false;
        } else {
          Toast("加载失败，请重试");
        }
      });
  },
  methods: {
    del(goodid, i) {
      Dialog.confirm({
        title: "删除提示",
        message: "您真的要删除么？"
      })
        .then(() => {
          this.$axios
            .post("/vue/delGood", {
              goodId: goodid,
              username: window.localStorage.getItem("users")
            })
            .then(res => {
              if (res.data.code == 200) {
                this.list.splice(i, 1);
              } else {
                Toast("加载失败，请重试");
              }
            });
        })
        .catch(() => {});
    },
    //提交 
    pay(goodid){
       // window.console.log(goodid)
      this.$axios.post("/vue/getpay",{goodid}).then(res=>{
       // window.console.log(res.data)
        if(res.data.code==200){
          Toast("提交成功")
this.$router.push({name:"pay",query:{goodid:goodid}});
        }else{
        Toast("加载失败，请重试")
        }
      })
    }

    // quan
