    //       document.querySelectorAll(".radio")[i].checked = false;
    //     }
    //   } else {
    //     for (let i = 0; i < this.list.length; i++) {
    //       document.querySelectorAll(".radio")[i].checked = true;
    //     }
    //   }
    //   this.checkedall = !this.checkedall;
    // },
    //单
    // check(){
    //   var len=document.querySelectorAll(".radio:checked").length;
    //   if(len==this.list.length){
    //     this.checkedall=true;
    //   }else{
    //     this.checkedall=false;

    //   }
    //   //money
    //   var price=0;
    //   for(let i=0;i<len;i++){
    //     price+=this.list[i].count*this.list[i].goodInfo["price"];
       

